import React, { Component } from 'react';
import './App.css';
import rockps from './contracts/rockps';
import web3 from './web3/web3';
import { Jumbotron, Button, Card, CardHeader, CardBody, CardText, CardFooter, Form, FormGroup, Label, Input } from 'reactstrap';

// Notes:
// Metamask only avec 1 account (the selected one) in accounts

const LOADING = '...loading';

const ST1 = {
  NOT_COMMITTED: 'Has not picked his/her choice yet',
  COMMITTED: 'Has sent his/her commitment',
};

const ST2 = {
  NOT_REVEALED: 'Has not revealed his/her choice yet',
  REVEALED: 'Has revealed his/her choice',
  PENDING: 'Waiting for the other player...',
};

const RESULT = {
  WON: 'Has won!',
  LOST: 'Has lost... ;-(',
  DRAW: 'It\'s a draw!'
};

const ACT = {
  SENDING: 'Sending transaction...',
  SENT: 'Transaction sent!',
  NONE: ''
};

const YES = 'Yes';
const NO = 'No';
const NA = 'N/A';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      players: [
        { addr: LOADING, status1: LOADING, status2: LOADING, act: LOADING }, 
        { addr: LOADING, status1: LOADING, status2: LOADING, act: LOADING }],
      playerCount: 0,
      canBeFinalised: LOADING,
      last: LOADING,
      choice0: 0,
      choice1: 0,
      nounce0: 0,
      nounce1: 0
    };

    this.handleChangeChoice = this.handleChangeChoice.bind(this);
    this.handleChangeNounce = this.handleChangeNounce.bind(this);
    this.handleChoice = this.handleChoice.bind(this);
    this.handleNounce = this.handleNounce.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleFinalSubmit = this.handleFinalSubmit.bind(this);
    this.newGame = this.newGame.bind(this);
  };

  async componentDidMount() {
    let canBeFinalised = NO;
    
    const getLastest = await rockps.methods.latestWinner().call();
    let gameCount = parseInt(await rockps.methods.gameCount().call());
    let last = 'game not played yet';
    if (gameCount !== 0) { 
      (getLastest === '0x0000000000000000000000000000000000000000') 
        ? last = 'draw'
        : last = getLastest ;
    };

    const playerCount = parseInt(await rockps.methods.getPlayerCount().call());
    const playerAddr = await rockps.methods.getPlayers().call();

    const players = [];
    switch(playerCount) {
      case 0:
        players.push({ addr: NA, status1: ST1.NOT_COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
        players.push({ addr: NA, status1: ST1.NOT_COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
        break;
      case 1:
        players.push({ addr: playerAddr[0], status1: ST1.COMMITTED, status2: ST2.PENDING, act: ACT.NONE });
        players.push({ addr: NA, status1: ST1.NOT_COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
        break;
      case 2:
        const hasRevealed = await rockps.methods.getRevealed().call();
        if (hasRevealed[0] && hasRevealed[1]) {
          players.push({ addr: playerAddr[0], status1: ST1.COMMITTED, status2: ST2.REVEALED, act: ACT.NONE });
          players.push({ addr: playerAddr[1], status1: ST1.COMMITTED, status2: ST2.REVEALED, act: ACT.NONE });
          canBeFinalised = YES;
        }
        else if (!hasRevealed[0] && !hasRevealed[1]) {
          players.push({ addr: playerAddr[0], status1: ST1.COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
          players.push({ addr: playerAddr[1], status1: ST1.COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
        }
        else {
          if (hasRevealed[0]) {
            players.push({ addr: playerAddr[0], status1: ST1.COMMITTED, status2: ST2.REVEALED, act: ACT.NONE});
            players.push({ addr: playerAddr[1], status1: ST1.COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
          }
          else {
            players.push({ addr: playerAddr[0], status1: ST1.COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE });
            players.push({ addr: playerAddr[1], status1: ST1.COMMITTED, status2: ST2.REVEALED, act: ACT.NONE });
          }
        }
        break;
      default:
    };

    this.setState({ players, playerCount, canBeFinalised, last }); // check if input should be added here
  };

  newGame = (last) => {
    this.setState({
      players: [
        { addr: NA, status1: ST1.NOT_COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE }, 
        { addr: NA, status1: ST1.NOT_COMMITTED, status2: ST2.NOT_REVEALED, act: ACT.NONE } ],
      playerCount: 0,
      canBeFinalised: NO,
      last: last,
      choice0: 0,
      choice1: 0,
      nounce0: '',
      nounce1: ''
    });
  };

  indexToChoice = (index) => { return 'choice' + index; };
  indexToNounce = (index) => { return 'nounce' + index; };

  handleChangeChoice = (event, index) => { 
    this.setState({ [this.indexToChoice(index)]: event.target.value }); 
  };
  handleChangeNounce = (event, index) => { 
    this.setState({ [this.indexToNounce(index)]: event.target.value }); 
  };

  handleChoice = (index) => { return this.state[this.indexToChoice(index)]; };
  handleNounce = (index) => { return this.state[this.indexToNounce(index)]; };

  handleSubmit = async (index, event) => {
    event.preventDefault();
    switch(index) {
      case 0:
        this.setState({ 
          players: [
            {...this.state.players[0], act: ACT.SENDING }, 
            this.state.players[1] ]
        });
        break;
      case 1:
        this.setState({ 
          players: [  
            this.state.players[0],
            {...this.state.players[1], act: ACT.SENDING}, ]
        });
        break;
      default:
    };
    const accounts = await web3.eth.getAccounts();
    const choice = (index===0) ? this.state.choice0 : this.state.choice1;
    const nounce = (index===0) ? this.state.nounce0 : this.state.nounce1;
    // Check type of choice and nouce in sha3 and impact on result

    const commit = web3.utils.soliditySha3(choice, nounce);
    // TODO: add address in the commit?

    await rockps.methods.enterCommitment(commit).send({ 
      from: accounts[0],
      value: 1000 
    });

    const players = (index===0) 
      ? [ {...this.state.players[0], 
          addr: accounts[0], act: ACT.SENT, status1: ST1.COMMITTED } ,
        this.state.players[1] ]
      : [this.state.players[0],
        {...this.state.players[1], 
          addr: accounts[0], act: ACT.SENT, status1: ST1.COMMITTED }, ] ;
          
    const playerCount = this.state.playerCount + 1;

    this.setState({ players, playerCount });
  };

  handleReveal = async (index, event) => {
    event.preventDefault();
    switch(index) {
      case 0:
        this.setState({ 
          players: [
            {...this.state.players[0], act: ACT.SENDING }, 
            this.state.players[1] ]
        });
        break;
      case 1:
        this.setState({ 
          players: [  
            this.state.players[0],
            {...this.state.players[1], act: ACT.SENDING}, ]
        });
        break;
      default:
    };

    const accounts = await web3.eth.getAccounts();
    const choice = (index===0) ? this.state.choice0 : this.state.choice1;
    const nounce = (index===0) ? this.state.nounce0 : this.state.nounce1;

    await rockps.methods.reveal(choice,nounce).send({ from: accounts[0] });

    const players = (index===0) 
      ? [ {addr: accounts[0], act: ACT.SENT, status2: ST2.REVEALED } ,
        this.state.players[1] ]
      : [this.state.players[0],
        {addr: accounts[0], act: ACT.SENT, status2: ST2.REVEALED }, ]; 
        
    const canBeFinalised = (this.state.players[0].status2 === ST2.REVEALED && this.state.players[1].status2 === ST2.REVEALED) ? YES : NO;

    this.setState({ players, canBeFinalised });
  };

  handleFinalSubmit = async () => {
    const accounts = await web3.eth.getAccounts();
    // this.setState({ canBeFinalised: 'Fetching winner... ' });

    await rockps.methods.finaliseGame().send({ from: accounts[0] });
    const last = await rockps.methods.latestWinner().call();

    (last === '0x0000000000000000000000000000000000000000')
      ? this.newGame('draw')
      : this.newGame(last);
  };

  handleReset = async () => {
    const accounts = await web3.eth.getAccounts();
    this.setState({ canBeFinalised: 'Resetting... ' });

    await rockps.methods.reset().send({ from: accounts[0] });

    const last = await rockps.methods.latestWinner().call();
    (last === '0x0000000000000000000000000000000000000000')
      ? this.newGame('game reset')
      : this.newGame(last);

  }

  render() {

    const FinaliseButton = () => {
      if (this.state.canBeFinalised === YES) {
        return (
          <p className="lead">
            <Button color="primary" onClick={() => this.handleFinalSubmit()}>Check winner</Button>
          </p>
        );
      }
      else { return( <div /> )}
    };
    
    const CommitForm = (props) => {
      return(
        <Form onSubmit={(event) => this.handleSubmit(props.index, event)}>
          <FormGroup>
            <Label for="choice">Pick a choice</Label>
            <Input type="select" id="choice" name="choice" 
            value={this.handleChoice(props.index)} 
            onChange={(event) => this.handleChangeChoice(event, props.index)}>
              <option value="0">Rock</option>
              <option value="1">Paper</option>
              <option value="2">Scissor</option>
            </Input>
            <Label for="nounce">Pick a secret number</Label>
            <Input type="text" id="nounce" name="nounce" 
            value={this.handleNounce(props.index)} 
            onChange={(event) => this.handleChangeNounce(event, props.index)}
            placeholder="your secret number">
            </Input>
          </FormGroup>
          <Button>Commit</Button>
        </Form>
      );
    };
    
    const RevealForm = (props) => {
      return(
        <Form onSubmit={(event) => this.handleReveal(props.index, event)}>
          <FormGroup>
            <Label for="choice">Pick your choice</Label>
            <Input type="select" id="choice" name="choice" 
            value={this.handleChoice(props.index)} 
            onChange={(event) => this.handleChangeChoice(event, props.index)}>
              <option value="0">Rock</option>
              <option value="1">Paper</option>
              <option value="2">Scissor</option>
            </Input>
            <Label for="nounce">Pick your secret number</Label>
            <Input type="text" id="nounce" name="nounce" 
            value={this.handleNounce(props.index)} 
            onChange={(event) => this.handleChangeNounce(event, props.index)}
            placeholder="your secret number">
            </Input>
          </FormGroup>
          <Button>Reveal</Button>
        </Form>
      );
    };
    
    const ShowAction = (props) => {
      if (props.player.ac !== ACT.NONE ) {
          return (<> {props.player.status} </>);
        }
      else { return (<> </>); }
    };

    const Commit = (props) => {
      if (props.player.status1 === ST1.NOT_COMMITTED) {
        return ( <CommitForm index={props.index}/> );
      }
      else {
        return (
          <>
            <CardText>{props.player.status1}</CardText> 
            <CardText>Contract used: {props.player.addr}</CardText>
          </>
        );
      }
    }

    const Reveal = (props) => {
      if ( props.player.status1 === ST1.COMMITTED
      && props.player.status2 === ST2.NOT_REVEALED) {
        return (
          <RevealForm index={props.index}/>
        );
      }
      else {
        return (
          <>
            <CardText>{props.player.status2}</CardText> 
          </>
        );
      }
    }

    const Player = (props) => {
      // const color = (props.player.playing === YES) ? 'primary' : 'secondary';

      return (
        // <Card outline color={color}>
        <Card>
          <CardHeader tag="h4">{props.name}</CardHeader>
          <div>
            <CardHeader tag="h6">Phase 1: Commit</CardHeader>
            <CardBody>
              <Commit 
                index={props.index} 
                player={props.player} />
            </CardBody>
          </div>
          <div>
            <CardHeader tag="h6">Phase 2: Reveal</CardHeader>
            <CardBody>
              <Reveal
                index={props.index} 
                player={props.player} />
            </CardBody>
          </div>
          <CardFooter className="text-muted">
            <ShowAction
              index={props.index} 
              player={props.player} />
          </CardFooter>
        </Card>
      );
    };

    const Header = () => {
      return(
        <Jumbotron>
          <div>
            <h1 className="display-3">Rock Paper Scissor</h1>
            <p className="lead">This is a simple rock paper scissor game that uses Ethereum's Rinkeby network. You should have metamask installed and configured on Rinkeby.</p>
          </div>
          <hr className="my-2" />
          <div>
            <h4>Game status</h4>
            <p>Currently <span className='js'>{this.state.playerCount}</span> players have submitted their choice (2 needed to finalise the round).</p>
            <p>Last game winner: {this.state.last}</p>
            <FinaliseButton />
            <p className="lead">
              <Button color="primary" onClick={() => this.handleReset()}>Reset Game</Button>
            </p>
          </div>
          <hr className="my-2" />
          <div>
            <h4>Instructions</h4>
            <p>Choose for both Alice and Bob (one after the other) what they should play.</p>
            <p>When the 2 players have sent their choice (button 'Send choice'), you can finalise the game (button 'Check winner' will appear).</p>
          </div>
        </Jumbotron>
      );
    };

    return (
      <div className="App">
        <div className="container">
          <Header />
          <div className="row justify-content-center">
            <div className="col-10 col-md-5 m-auto">
              <Player 
                name='Alice' 
                index={0}
                player={this.state.players[0]}/>
            </div>
            <div className="col-10 col-md-5 m-auto">
              <Player 
                name='Bob' 
                index={1} 
                player={this.state.players[1]}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
};

export default App;
